body {
  font-family: 'Poppins' !important;
}

.App {
    width:100%;
    padding-top: 30px; 
}
.leftContainer {
    width: 70%;
    float: left;
    padding: 0 17%;
}
.rightContainer {
    width: 25%;
    float: right;
    margin-right: 50px;
}
.App h3 {
    text-align: center;
    margin-bottom: 15px;
    font-size: 21px;
    font-weight: bold;
    text-transform: uppercase;
}
.justifyContent {
    justify-content: end;
}
.btn-secondary {
    margin-right: 15px;
}
.summaryPage {
    font-size: 13px;
}
.summarySection {
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: 15px;
    line-height: 25px;
}
.summarySection svg {
    position: relative;
    top: 5px;
    left: 20px;
    color: #4a5258;
}
.summarySection.highlighted {
    border-left: 5px solid #18ACA7;
}
footer {
    font-size: 13px;
    position: fixed;
    bottom: 0;
    text-align: center;
    width: 100%;
}
footer div {
    background: #f1f2f3;
    padding: 10px 0;
}
.welcomePosition {
    margin: 0;
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
}
.info {
    font-size: 13px;
    font-style: italic;
}
.summarySection hr {
    width: 95%;
    margin: 0 auto !important;
}
.summarySection b {
    text-transform: uppercase;
}
input[type="text"], input[type="number"], select {
    border: 1px solid #ccc;
}
.btnColor {
    background: #18ACA7 !important;
    border-color: #18ACA7 !important;
}
